import React from "react";
import Card from "../Card/Card";
import importAllImages from '../Helpers/ImportAllImages';
import barsCardData from './BarsData.json';

const barsImages = importAllImages(require.context('../PartnersLogos/Bars', false, /\.(svg)$/));

const Bars = () => {
    return (
        <>
            <h2>Bars</h2>
            <div className="section-container">
                {barsImages.map((barImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index} // Adding key prop for each Card component
                        image={barImage}
                        partner_name={barsCardData.partner_name[index]}
                    />

                ))}
            </div>
        </>
    );
}

export default Bars;